@import '/src/theme/_base';

.container {
  &__maskDetectionComponent {
    position: relative;
    width: 100VW;
    height: 100VH;
    overflow: hidden;
  }

  &__header {
    color: #000;
    text-align: center;
    font-family: "Helvetica Neue";
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px; 
    background: white;
    padding: 10px 0;
  }

  &__flexBox {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  &__rotatedMessage {
    position: absolute;
    bottom: 25px;
    left: 6%;
    background-color: white;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.15);
    padding: 2px 8px;
    font-size: 16px;
  }

  &__videoContainer {
    display: flex;
    justify-content: center;
  }
  
  &__videoElement {
    width: 53%;
    height: 100%;
    position: relative;
  }

  &__suggestionMessage {
    position: absolute;
    bottom: 10%;
    background-color: white;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.15);
    padding: 2px 8px;
    font-size: 16px;
  }

  &__buttonAlign {
    position: absolute;
    bottom: 3%;
    right: 5%;
  }

  &__title {
    color: #000;
    text-align: center;
    font-family: "Helvetica Neue";
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px; /* 125% */
    letter-spacing: -0.48px;
    margin: 46px 0;
  }

  &__iconsBox {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  &__arrowIcon {
    margin-top: 28px;
  }


}
