@import '/src/theme/_base';

.container {
  &__flexBox {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 16px;
    height: 100vh;
    justify-content: end;
  }

  &__title {
    color: #000;
    text-align: center;
    font-family: "Helvetica Neue";
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px; /* 125% */
    letter-spacing: -0.48px;
    margin: 46px 0;
  }

  &__iconsBox {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  &__arrowIcon {
    margin-top: 28px;
  }
}
